.floor-plans {
  -ms-flex-align: center;
      align-items: center;
  background-color: #f8f4ec;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  padding-bottom: 10px
}

.floorplan-header {
  margin: 10px;
  padding: 10px;
}

.model {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  background-color: white;
  margin: 10px;
  padding: 5px;
}

.floor-title {
  display: block;
  margin: 5px;
  padding: 5px;
}

.left-model {
  border-right: 2px solid #00abf0;
  display: -ms-flexbox;
  display: flex;
  height: auto;
  -ms-flex-pack: left;
      justify-content: left;
  -ms-flex-align: center;
      align-items: center;
  width: 500px;
}

.line-break {
  border-bottom: 1.5px solid #00abf0;
  opacity: 0.4;
  width: 210%;
}

table {
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  padding: 0px 10px;
  width: 100%;
}

td {
  padding: 5px 0;
}

.img-desc {
  text-align: center;
}

.floor-img {
  height: 300px;
  width: 600px;
  cursor: pointer;
}

.modal-title1 {
  font-size: 28px;
}

.modal-title2 {
  font-size: 28px;
}

.close {
    position: absolute;
    top: 15px;
    right: 35px;
    color: black;
    font-size: 40px;
    font-weight: bold;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    cursor: pointer;
}


@media (max-width: 1119px) {
  .floorplan-header {
    font-size: 28px;
  }

  .model {
    -ms-flex-direction: column;
        flex-direction: column;
    width: 90%;
  }

  .left-model {
    width: 100%;
    border: none;
  }

  .floor-img {
    width: 100%;
  }
}

@media (max-width: 700px) {
  .img-desc {
    display: none;
  }
}

@media (max-width: 400px) {
  .floorplan-header {
    font-size: 27px;
  }
}





